*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}


.content{
  height: 120vh;
}

.navbar{
  background-color: rgba(113, 225, 126, 0.621) !important;
}

.register-page{
  display:flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background: url(./Asset/Images/RegisterProfile1.jpg) no-repeat center;
  background-size: cover;
}

.register-form{
  background-color: rgba(255, 255, 255, 0.211);
  box-shadow: 0px 8px 32px 0 rgba(13, 13, 13, 0.394) ;
  backdrop-filter: blur(3px);
  margin-right: 60%;
  width: 26vw;
  padding: 0px 16px 16px 16px;
  border-radius: 16px;
}

.login-page{
  display:flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background: url(./Asset/Images/LoginProfile.jpg) no-repeat center;
  background-size: cover;
}
.login-form{
  background-color: rgba(255, 255, 255, 0.211);
  box-shadow: 0px 8px 32px 0 rgba(228, 217, 203, 0.382) ;
  backdrop-filter: blur(3px);
  width: 20vw;
  padding: 0px 16px 16px 16px;
  border-radius: 16px;
  color: aliceblue;
}
/* transection page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* antc icons */
.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.783);
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: rgb(79, 169, 38);
}

.inactive-icon{
  color: black;
}